import React from 'react'
import { X } from 'react-feather'

const Sidedrawer = ({ showSidedrawer, sidedrawerHandler, photosTrayHandler }) => {
  return (
    <React.Fragment>
      {showSidedrawer && <div className="back-drop" onClick={sidedrawerHandler} />}
      <div className={showSidedrawer ? 'side-drawer bg-white show shadow' : 'side-drawer bg-white'}>
        <span className="menu-btn mt-3 ml-3" title="Close" onClick={sidedrawerHandler}>
          <X size={'30'} />
        </span>
        <ul className="list-group mt-10">
          <li
            className="list-group-item"
            onClick={() => {
              photosTrayHandler()
              sidedrawerHandler()
            }}>
            <span>Photos</span>
          </li>
        </ul>
      </div>
    </React.Fragment>
  )
}

export default Sidedrawer
